.body-top{
    height: 100px;
    
}
.bodyBottomTop{
    padding: 30px;
}
.bodytopcontainer{
    display: flex;
    align-items: center;
    color: gray;
}
.bodyBottom{
    width: 800px;
    margin: 20px auto 0px auto;
}
.body-input{
    width: 100%;
    height: 40px;
    border-radius: 5px;
   
  
    background-color: white;
    display: flex;
    

}
input{
    border-style: none;
    width: 90%;
    margin-left: 20px;
}
input:focus{
    outline: none;
}
.body-text{
    text-align: center;
    margin-top: 30px;
}
.body-card{
    width: 200px;
    height: 200px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: white;
    font-weight: bold;
}
.body-bottom-container{
    background-color:#f6f5f5;
    height: calc(100vh - 100px);

}


@media(max-width:1103px){
    .bodyBottom{
        width: 100%;
        margin: 0px;
    }
    .body-bottom-container{
       padding: 10px;
    }
}
